.offers-to-staff {
  padding: 58px 0 56px 0;

  @media (min-width: $l-width) {
    padding: 40px 0 50px 0;
  }

  & .offers-to-staff__benefits {
    @media (min-width: $l-width) {
      padding: 47px 40px 82px 75px;
    }
  }
}

.offers-to-staff__header {
  display: block;
  padding: 0 30px 0 30px;
  margin-bottom: 30px;

  @media (min-width: $l-width) {
    width: 715px;
    padding: 0;
    margin: 0 auto 50px 330px;
  }
}

.offers-to-staff__benefits {
  position: relative;
  margin-left: auto;
  top: -40px;
  z-index: 2;

  @media (min-width: $l-width) {
    grid-column: 2/4;
    grid-row: -1/1;
    top: 0;
  }
}

.offers-to-staff__wrapper {
  position: relative;

  @media (min-width: $l-width) {
    display: grid;
    grid-template-columns: 780px 2px auto;
  }
}

.offers-to-staff__slider-wrapper {
  @media (min-width: $l-width) {
    width: 800px;
    grid-column: 1/2;
    grid-row: -1/1;
  }
}

.offers-to-staff__button {
  position: absolute;
  left: 60px;
  bottom: 60px;
}
