.page-header {
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  padding: 23px 0 30px 20px;
  background-color: $page-background;
  z-index: 1;

  @media (min-width: $l-width) {
    padding: 88px 60px 40px 60px;
    position: static;
  }
}

.page-header__logo {
  width: 106px;
  height: 30px;

  @media (min-width: $l-width) {
    width: 150px;
    height: 40px;
  }
}

.page-header__link {
  display: none;
  width: 20px;
  height: 20px;

  @media (min-width: $l-width) {
    display: block;
  }
}
