.working-with-staff {
  position: relative;
  padding: 45px 0 0 0;
  background-color: $body-background;
  z-index: 1;

  @media (min-width: $l-width) {
    padding: 45px 0 0 0;
  }
}

.working-with-staff__header {
  display: block;
  width: 85%;
  padding-left: 20px;
  margin-bottom: 26px;

  @media (min-width: $l-width) {
    width: auto;
    padding-left: 60px;
    margin-bottom: 50px;
  }
}

.working-with-staff__slider-wrapper {
  position: relative;
  box-sizing: border-box;
  width: 93%;
  padding: 32px 0 0 0;
  background-color: $page-background;
  margin-left: auto;
  margin-bottom: 33px;

  @media (min-width: $l-width) {
    width: 1190px;
    padding: 0;
    margin-bottom: 54px;
  }
}

.working-with-staff__decoration-line {
  @media (min-width: $l-width) {
    width: 300px;
  }
  margin: 0;
  width: 10px;
  // margin-bottom: 19px;
}
