.offers-staff-slider {

  & img {
    width: 83%;
    height: auto;

    @media (min-width: $l-width) {
      width: 920px;
      height: auto;
    }
  }

  & .swiper-pagination {
    width: 150px;
    left: 50%;
    transform: translate(-65%);
    display: flex;
    justify-content: space-between;
    align-items: center;
    bottom: 50px;

    @media (min-width: $l-width) {
      display: none;
    }
  }

  & .swiper-pagination-bullet {
    width: 6px;
    height: 6px;
    background-color: $page-background;
  }

  & .swiper-pagination-bullet-active {
    width: 10px;
    height: 10px;
  }

  @media (min-width: $l-width) {
    padding-top: 50px;
  }
}
