.our-projects {
  padding-top: 33px;

  & .our-projects__header {
    @media (min-width: $l-width) {
      margin-bottom: 15px;
    }
  }
}

.our-projects__description {
  min-width: 250px;
  padding: 0 20px 22px 30px ;

  &.our-projects__description--bottom {
    padding-bottom: 40px ;

    @media (min-width: $l-width) {
      padding-top: 45px ;
      padding-bottom: 47px ;
    }
  }

  & p {
    @include indent-reset;
  }

  @media (min-width: $l-width) {
    padding: 65px 200px 55px 330px ;
  }
}

.our-projects__wrapper {
  @media (min-width: $l-width) {
    position: relative;
    display: grid;
    grid-template-columns: 780px 140px 330px;
    grid-template-rows: 426px min-content;
  }
}


.our-projects__slider-wrapper {
  width: auto;

  @media (min-width: $l-width) {
    width: 920px;
    grid-column: 1/3;
    grid-row: 1/-1;
    min-width: 0;
  }
}

.our-projects__presentation {
  position: relative;
  box-sizing: border-box;
  width: 93%;
  padding: 24px 20px 20px 20px;
  background-color: $page-background;
  margin-left: auto;
  top: -32px;
  margin-bottom: -5px;
  z-index: 2;

  & span {
    display: block;
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 8px;

    @media (min-width: $l-width) {
      font-size: 18px;
      line-height: 26px;
    }
  }

  & p {
    @include indent-reset;
  }

  @media (min-width: $l-width) {
    width: 100%;
    grid-column: 2/4;
    grid-row: 1/2;
    top: 0;
    padding: 59px 60px 43px 51px;
    height: auto;
    margin-top: 36px;
  }
}

.our-projects__button {
  position: absolute;
  right: 60px;
  bottom: 65px;
}
