.page-body {
  position: relative;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  color: $text-color;
  min-width: 300px;
  margin: 0 15px;

  @media (min-width: $s-width) {
    width: 610px;
    margin: 0 auto;
  }

  @media (min-width: $m-width) {
    width: 930px;
    margin: 0 auto;
  }

  @media (min-width: $l-width) {
    width: 1250px;
    margin: 0 auto;
    font-size: 16px;
    line-height: 24px;
  }

  &::before {
    position: absolute;
    content: "";
    width: 1px;
    height: 100%;
    left: 10px;
    background-color: $line-color;
    z-index: 0;

    @media (min-width: $l-width) {
      bottom: 0;
      width: 2px;
      left: 298px;
      height: 98.9%;
    }
  }
}
