.typical-list {
  @include indent-reset;
  box-sizing: border-box;
  width: 93%;
  padding: 25px 20px 29px 40px;
  background-color: $page-background;

  @media (min-width: $l-width) {
    width: 470px;
    padding: 63px 40px 60px 85px;
  }

  & p {
    @include indent-reset;
  }

  & li {
    position: relative;
  }

  & li::before {
    position: absolute;
    content: "";
    width: 12px;
    height: 1px;
    top: 10px;
    left: -20px;
    background-color: $header-color;

    @media (min-width: $l-width) {
      width: 16px;
      height: 2px;
      top: 12px;
      left: -25px;
    }
  }

  & li:not(:last-child) {
    margin-bottom: 8px;

    @media (min-width: $l-width) {
      margin-bottom: 10px;
    }
  }
}
