.who-we-are {
  padding: 134px 0 0 0;
  background: url("../img/decorative/main-background-mobile.png") top right no-repeat;
  background-size: 93% auto;

  @media (min-resolution: 2dppx) {
    background: url("../img/decorative/main-background-mobile@2x.png") top right no-repeat;
    background-size: 93% auto;
  }

  @media (min-width: $l-width) {
    padding: 65px 0;
    background-size: 920px auto;

    background: url("../img/decorative/main-background-desktop.png") top right no-repeat;
    background-size: 920px auto;

    @media (min-resolution: 2dppx) {
      background: url("../img/decorative/main-background-desktop@2x.png") top right no-repeat;
      background-size: 920px auto;
    }
  }

  & p {
    @include indent-reset;
  }

  & p:not(:last-child) {
    margin-bottom: 8px;
  }
}

.who-we-are__wrapper {
  position: relative;
  box-sizing: border-box;
  width: 93%;
  padding: 35px 20px 40px 20px;
  background-color: $page-background;
  z-index: 1;

  @media (min-width: $l-width) {
    width: 550px;
    padding: 48px 30px 54px 60px;
  }
}
