.we-are-now__description {
  position: relative;
  padding: 35px 0 25px 18px;
  background-color: $body-background;
  z-index: 1;

  @media (min-width: $l-width) {
    padding: 42px 60px 48px 60px;
  }
}

.we-are-now__wrapper {
  width: 82%;
  display: grid;
  grid-template-columns: 14fr 10fr;
  grid-template-rows: min-content min-content;
  column-gap: 20px;
  row-gap: 17px;

  @media (min-width: $l-width) {
    width: 100%;
    grid-template-columns: 521px 1fr 1fr;
    column-gap: 65px;
    row-gap: 0;
  }
}

.we-are-now__lacation {
  grid-column: 1/2;
  grid-row: 1/-1;

  & p {
    @include indent-reset;
  }
}

.we-are-now__activities {
  position: relative;
  margin-right: auto;
  z-index: 1;
}

.we-are-now__activities-wrapper {
  padding: 0 0 164px 0;
  background: url("../img/decorative/meeting.jpg") right bottom 20px no-repeat;
  background-size: 93% auto;

  @media (min-resolution: 2dppx) {
    background: url("../img/decorative/meeting@2x.jpg") right bottom 20px no-repeat;
    background-size: 93% auto;
  }

  @media (min-width: $l-width) {
    padding: 35px 0 88px 0;
    background: url("../img/decorative/meeting-desktop.jpg") right top no-repeat;
    background-size: 920px auto;

    @media (min-resolution: 2dppx) {
      background: url("../img/decorative/meeting-desktop@2x.jpg") right top no-repeat;
      background-size: 920px auto;
    }
  }
}

.we-are-now__decoration-line.we-are-now__decoration-line--top {
  @media (min-width: $l-width) {
    width: 300px;
    margin-bottom: 0;
  }
  margin: 0;
  width: 10px;
  margin-bottom: 19px;
}
