.staff-slider {

  padding-bottom: 45px;

  @media (min-width: $l-width) {
    padding: 0;

  }

  & .swiper-pagination {
    position: absolute;
    width: 80px;
    left: 50%;
    transform: translate(-50%);
    bottom: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (min-width: $l-width) {
      display: none;

    }
  }

  & .swiper-pagination-bullet {
    width: 6px;
    height: 6px;
    background-color: $header-color;
  }

  & .swiper-pagination-bullet-active {
    width: 10px;
    height: 10px;
  }
}

