.staff-slider__header {
  @include indent-reset;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  margin-bottom: 10px;

  @media (min-width: $l-width) {
    font-size: 18px;
    line-height: 26px;
    margin-bottom: 12px;
  }
}

.staff-slider__slide {
  box-sizing: border-box;
  min-height: 300px;
  padding: 0 20px 0 20px;
  background-color: $page-background;
  margin-left: auto;

  @media (min-width: $l-width) {
    width: 1150px;
    padding: 45px 50px 80px 50px;
  }
}

.staff-slider__decorative-line {
  width: 100%;
  height: 3px;
  background: url("../img/decorative/divider-mobile.png")  no-repeat;
  margin-bottom: 24px;

  @media (min-width: $l-width) {
    background: url("../img/decorative/divider.png")  no-repeat;
    margin-bottom: 35px;
  }
}

.staff-slider__description {
  @include indent-reset;
}

.staff-slider__button {
  position: absolute;
  z-index: 2;
  right: 60px;
  top: 48px;
}
