.structure {
  width: auto;

  & p {
    @include indent-reset;
  }
}

.structure__quantity {
  text-transform: uppercase;

}

.structure__figure {
  display: block;
  font-size: 36px;
  line-height: 32px;
  margin-bottom: 5px;
  font-weight: 700;

  @media (min-width: $l-width) {
    display: inline-block;
    font-size: 48px;
    line-height: 42px;
    margin-bottom: 0;
  }
}
